#container {
  flex: 1;
}

#mapContainer {
  height: 100vh;
}

.tooltip {
  width: 25%;
  max-width: 300px;
  float: left;
  text-align: center;
  transition: all 0.5s ease;
  background-color: #f5f5f5;
  position: absolute;
  border-right: 1px solid #d2d2d2;
  z-index: 999999;
  color: #2e2e2e;
  font-family: sans-serif;
  overflow: scroll;
}

.row {
  flex: 1 1;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
}

.row:nth-child(even) {
  background: #ccc;
}
.row:nth-child(odd) {
  background: #fff;
}
.row p {
  text-align: left;
  margin:10px;
}
.row p:nth-child(even) {
  font-weight: bold;
  text-align: right;
}

.singleRow {
  color: white;
  font-weight: bold;
  text-align: center;
  background-color: #389a05;
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
}

button.blueButton {
  border: none;
  background: #3388ff;
  color: white;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
}
.closeButton {
  width: 100%;
}

.pageName {
  padding: 10px;
  background-color: #276f00;
  color: white;
  font-weight: bold;
  text-align: center;
  font-size: 20px;
}

.linkButton {
  right: 0px;
  float: right;
  position: absolute;
  top: 0px;
}

.goToSimulator {
  width: 100%;
  background: #f05624 !important;
}

.legendContainer {
  position: absolute;
  width: 25%;
  z-index: 99999;
  right: 0;
  background-color: rgba(255,255,255,0.85);
  padding: 10px;
  max-height: 100%;
  overflow:scroll;
}

.legendColor {
  width: 100%;
  height: 20px;
  position: relative;
}

.legendTitle {
  padding: 4px;
  background-color: #296f01;
  color: white;
  border-radius: 2px;
}
.otherLegend {
  margin: 2px;
  background-color: #498725;
}
.simulator {
  transition: all 0.5s ease;
  position: absolute;
  height: 100%;
  background: white;
  text-align: center;
  overflow: scroll;
  z-index: 999999;
  border: 1px solid #f2efe9;
  width:45%;
  left: 45%;
  transform: translateX(-45%);
}

.disclaimer{
  font-size: small;
  text-align: justify;
  font-style: italic;
  padding-right: 8px;
  padding-left: 8px;
}
.selectedLegend {
  background: #dddddd;
  padding:6px;
  margin-bottom: 6px;
}
.selectedLegend p {
  margin-top: 0px;
}

/* TABLE */
table.greenRows {
  width: 100%;
  height: 200px;
  text-align: center;
  border-collapse: collapse;
  margin-top:30px;
}
table.greenRows td, table.greenRows th {
  /* border: 1px solid #AAAAAA; */
  padding: 3px 2px;
}
table.greenRows tr:nth-child(even) {
  background: #CCCCCC;
}
table.greenRows thead {
  background: #296f01;
  border-bottom: 1px solid #ccc;
}
table.greenRows thead th {
  font-size: 15px;
  font-weight: bold;
  color: #FFFFFF;
  text-align: center;
  border-left: 1px solid #ccc;
}
table.greenRows thead th:first-child {
  border-left: none;
}

table.greenRows tfoot td {
  font-size: 14px;
}
input[type="number"] {
  height: 25px;
  border: none;
  padding: 2px;
}
td {
  background: #ccc;
  border-bottom: 1px solid white;
  border-right: 1px solid white;
}

.resultName{
text-align: left;
margin-bottom:20px;
}
.resultVal{
  font-weight: bold;
}
.resultName p {
  float: left;
  margin-top: 6px;
  margin-bottom: 6px;
}
.singleRow p {
  margin-top: 8px;
  margin-bottom: 8px;
}
img.info {
  height: 30px;
  width: 30px;
  margin-right: 50px;
  float: right;
  margin-top: -5px;
}